.body {
    width: 100%;
    height: 90vh;
    cursor: default;
}

.scroll {
    height: auto;
    overflow-y: scroll;
    scroll-snap-type: y mandatory;
}

.scroll section {
    scroll-snap-align: start;
    position: relative;
}

.sectionIntro {
    width: 100%;
    height: 85vh;
    display: inline-flex;
    flex-direction: row;
    align-items: center;
}

.splash {
    width: 60%;
}

.top_arrow {
    width: 15px;
    height: 15px;
    position: fixed;
    border-right: 3px solid black;
    border-top: 3px solid black;
    transform: rotate(-45deg);
    margin-left: 95%;
    margin-top: 60px;
    cursor: pointer;
}

.intro_description {
    width: 65%;
    margin-right: 5%;
    text-align: left;
    padding-left: 2%;
    border-left: 10px solid #ffc4c4;
}

.intro_subtitle {
    font-size: 450%;
}

@media screen and (max-width: 1400px) { /* for screen size less or equal to 320px*/
    .intro_subtitle {
       font-size: 350%; /*Changes as per your requirement*/
    }    
}

@media screen and (max-width: 1100px) { /* for screen size less or equal to 320px*/
    .intro_subtitle {
       font-size: 300%; /*Changes as per your requirement*/
    }    
}

.space_divider {
    height: 5vh;
}

.sectionPrototype, .sectionArt, .sectionAbout {
    width: calc(80% - 10px);
    margin-left: calc(20% - 10px);
    height: 100%;
    display: inline-block;
    flex-direction: row;
    padding-left: 10px;
    margin-right: 0;
    cursor: default;
}

.main_title {
    font-size: 250%;
    text-align: left;
    border-bottom: 5px solid #ffc4c4;
}

.sub_title {
    font-size: 36px;
}

.date {
    font-size: 20px;
}

.display {
    width: 100%;
    min-height: 100vh;
}

.subsection {
    display: inline-flex;
    height: 90vh;
}

.subsection_col {
    width: 210px;
    height: 100vh;
    margin: 0 15% 10% -25%;
    padding: 0px 25px 0px 25px;
    border-right: 10px solid transparent;
    border-image: linear-gradient(0deg, #ffffff, #bee7b8);
    border-image-slice: 1;
}

.showcase {
    width: calc(80% - 20px);
    max-height: 100vh;
    display: inline;
    text-align: left;
    overflow-y: scroll;
}

.model_showcase {
    width: calc(80% - 20px);
    height: 100vh;
    display: inline-flex;
    justify-content: left;
    flex-wrap: wrap;
    align-content: flex-start;
}

.desktop_sample {
    width: calc(95% - 30px);
    max-width: 70%;
    border: 5px solid lightgrey;
    padding: 5px;
    border-radius: 25px;
    margin-left: calc(17% - 30px);
}

.mobile_img {
    height: 50%;
    border: 10px solid black;
    padding: 5px;
    border-radius: 25px;
    margin-left: 34%;
}

.showcase_description {
    width: 70%;
    font-size: 20px;
    display: inline-flex;
    text-align: center;
    margin-left: 15%;
}

.info_col {
    width: 100%;
    display: flex;
    align-items: center;
}

.decor_arrow, .decor_arrow2, .decor_arrow3 {
    width: 15px;
    height: 15px;
    position: absolute;
    border-right: 5px solid #bee7b8;
    border-top: 5px solid #bee7b8;
    transform: rotate(45deg);
    margin-top: 12px;
    margin-left: 235px;
}

.arrow_row {
    width: 15px;
    display: flex;
    position: absolute;
    margin-top: 0px;
}

.arrow_instruct {
    font-size: 18px;
    text-align: left;
    margin-left: 15px;
    bottom: 10px;
}

.arrow {
    width: 15px;
    height: 15px;
    position: relative;
    border-right: 3px solid black;
    border-top: 3px solid black;
    transform: rotate(135deg);
    margin-left: 55px;
    margin-top: -10px;
}

.up_arrow {
    width: 15px;
    height: 15px;
    position: relative;
    border-right: 3px solid black;
    border-top: 3px solid black;
    transform: rotate(-45deg);
    margin-left: 55px;
    cursor: pointer;
}

.up_arrow_row {
    width: 15px;
    margin-top: -40px;
}

.sectionMore {
    width: 80%;
    height: 100vh;
    display: inline-block;
    justify-content: center;
}

.case_study_intro {
    display: inline-flex;
    padding-bottom: 30px;
    border-bottom: 1px solid black;
}

.sum_title {
    font-size: 60px;
    line-height: 1;
}

.proj_sum {
    text-align: left;
}

.sum {
    line-height: 2;
}

.kingpin_sample2 {
    width: 50%;
    border: 10px solid black;
    padding: 5px;
    border-radius: 25px;
}

.case_study {
    width: 60%;
    height: 100vh;
    display: inline-block;
    justify-content: center;
}

.showcase_img {
    display: inline-block;
    height: 29%;
    margin: 5px;
}

iframe {
    margin: 5px;
}



/* ABOUT ---------------------------------------------- */


.pfp {
    width: 350px;
    margin-right: 5%;
    border-radius: 25px;
}

.about_showcase {
    width: calc(80% - 20px);
    height: 30vh;
}

.about_intro {
    display: inline; /*inline-flex sidebyside*/
    justify-content: center;
}

.about_description {
    width: 70%;
    font-size: 20px;
    display: inline-flex;
    text-align: center;
}

.contact_icons {
    display: block;
    margin-top: 10px;
}

.icon {
    margin-top: 10px;
}