.header {
  width: 100%;
  height: 10%;
  display: inline-block;
  flex-direction: row;
  justify-content: space-between;
  position: relative;
  background-color: #bee7b8;
}

.nav {
  width: 100%;
  height: 120%;
  display: flex;
  overflow: hidden;
  justify-content: space-around;
  align-items: center;
  position: relative;
  flex-wrap: wrap;
}

.links {
  width: 55%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  overflow: scroll;
}

/* link urls */
.links a {
  padding: 30px;
  color: black;
  display: inline-block;
  padding: 10px 20px;
  position: relative;

  color: black;
  font-size: 18px;
  text-align: center;
  text-decoration: none;
}

/* animation */
.link:after {  
  content: "";

  width: 0;
  height: 2px;
  left: 50%;
  bottom: 0;
  display: block;
  position: absolute;

  background: none repeat scroll 0 0 transparent;
  background: #9797cf;
  transition: width 0.5s ease 0s, left 0.5s ease 0s;
}

.link:hover:after { 
  width: 100%; 
  left: 0; 
}

.active{
  color: black;
  font-weight: bold;
  pointer-events: none;
}

.icon {
  border: 1.5px solid #272445;
  border-radius: 50%;
  width: 35px;
}

.icon:hover {
  box-shadow: 0 0 8px #272445;
  border-radius: 50%;
}
  
.nav {
  height: 8vh;
  display: flex;
  align-items: center;
  position: -webkit-sticky;
  position: sticky;
  top: -5px;
  z-index: 10;
  width: calc(100%);
  background-color: #bee7b8;
  box-shadow: 0 0.125rem 0.25rem 0 rgb(0 0 0 / 11%);
}

.nav_sections {
  display: inline-flex;
  flex-flow: row nowrap;
  padding-left: 50px;
  flex: 1;
}

.active {
  font-weight: bold;
}

ul {
  display: flex;
  gap: 40px;
  font-size: 0.9rem;
  list-style: none;
  padding: 0;
}

li {
  cursor: pointer;
  padding: 30px;
  color: black;
  display: inline-block;
  padding: 10px 20px;
  position: relative;

  color: black;
  font-size: 18px;
  text-align: center;
  text-decoration: none;
}

li:hover {
  text-decoration: underline;
  text-underline-offset: 5px;
}

li:hover:after { 
  width: 100%; 
  left: 0; 
}

.update {
  float: left;
  margin-left: 2.5%;
  color: #c8ccc8;
}